@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+Display:wght@100;300;400&display=swap);

body {
  background: linear-gradient(gray -50%, black);
}

html,
body {
  height: 100%;
  margin: 0;
}

.description {
  display: flex;
  font-family: "Noto Sans Regular", sans-serif;
  color: white;
  padding: 10px;
}

.descriptionImage {
  width: 300px;
  height: auto;
}

.descriptionText {
  margin-left: 30px;
}

.mainDescription {
  font-size: 50px;
}

.text {
  margin-top: 10px;
}

.buttonPause {
  background-color: #1db954;
  color: white;
  border: 0px;
  padding: 5px 35px;
}

.buttonEllipsis {
  background-color: Transparent;
  border-radius: 50%;
  border-color: white;
  border-style: solid;
  margin-left: 10px;
}

button {
  border-radius: 30px;
}

.SongString {
  display: flex;
  justify-content: space-around;
  color: white;
  margin-top: 10px;
}

.SongString button {
  width: 70px;
  border: none;
  background-color: #1db954;
  color: white;
}

.songInfo {
  width: 300px;
}

